@import 'styles/shared.scss';

.section {
  padding: 0 0 30px 0;
  color: #e0e0e0;

  h1 {
    padding: 0 0 20px 0;
    color: #fff;
  }
}

.useList {
  padding: 30px 0;
  border-top: 1px solid #666;
  color: $primaryColor;

  li {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }
}

.toast {
  @media screen and (max-width: 479px) {
    padding: 5px;
  }

  @media screen and (min-width: 480px) {
    width: 300px;
  }
}
